<template>
  <div class="container " style="min-height: 597px;">

    <div class="block-list address-list section section-first js-no-webview-block" 
    v-if="lists && lists.length">
      <a class="block-item js-address-item address-item " 
      @click="toEdit(list)" 
      v-for="list in lists" 
      :key="list.id" 
      :class="{'address-item-default': list.isDefault }">
        <div class="address-title">{{list.name}} {{list.tel}}</div>
        <p>{{list.provinceName}}{{list.cityName}}{{list.districtName}}{{list.address}}</p>
        <a class="address-edit">修改</a>
      </a>
    </div>

    <div v-if="lists && !lists.length">
        没有地址，请添加
    </div>

    <div class="block stick-bottom-row center">
      <router-link :to="{name: 'form', query:{type: 'add'}}" 
      class="btn btn-blue js-no-webview-block js-add-address-btn" >
            新增地址
        </router-link>
    </div>

  </div>
</template>

<script>

import axios from 'axios'
import url from '../api.js'
export default {
    data(){
       return {
           lists: null
       }
    },
    created() {
        axios.get(url.addressList).then(res => {
            this.lists = res.data.lists                       
        })
    },

    methods:{
        toEdit(list){
            // $router是VueRouter的实例方法，包含多个路由，每个路由包括名称，路径，组件等等
            this.$router.push({name: 'form', query:{
                type: 'edit',
                instance: list,
            }})
        },

    },
}
</script>